html, body, #root, .app {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.app {
    min-height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
  }

.card {
  background: white;
  width: 25rem;
  height: 35rem;
  padding: 1.5rem;
  box-shadow: 0 0.2rem 1rem rgba(0, 0, 0, 1);
}
.app__background {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background:
    -webkit-radial-gradient(60% 43%, circle closest-side, rgb(255, 211, 223) 26%, rgba(187,0,51,0) 27%),
    -webkit-radial-gradient(40% 43%, circle closest-side, rgb(255, 211, 223) 26%, rgba(187,0,51,0) 27%),
    -webkit-radial-gradient(40% 22%, circle closest-side, rgb(248, 195, 206) 45%, rgba(221,51,85,0) 46%),
    -webkit-radial-gradient(60% 22%, circle closest-side, rgb(248, 195, 206) 45%, rgba(221,51,85,0) 46%),
    -webkit-radial-gradient(50% 35%, circle closest-side, rgb(248, 195, 206) 30%, rgba(221,51,85,0) 31%),

    -webkit-radial-gradient(60% 43%, circle closest-side, rgb(255, 211, 223) 26%, rgba(187,0,51,0) 27%) 50px 50px,
    -webkit-radial-gradient(40% 43%, circle closest-side, rgb(255, 211, 223) 26%, rgba(187,0,51,0) 27%) 50px 50px,
    -webkit-radial-gradient(40% 22%, circle closest-side, rgb(248, 195, 206) 45%, rgba(221,51,85,0) 46%) 50px 50px,
    -webkit-radial-gradient(60% 22%, circle closest-side, rgb(248, 195, 206) 45%, rgba(221,51,85,0) 46%) 50px 50px,
    -webkit-radial-gradient(50% 35%, circle closest-side, rgb(248, 195, 206) 30%, rgba(221,51,85,0) 31%) 50px 50px;
  background:
    radial-gradient(circle closest-side at 60% 43%, rgb(255, 211, 223) 26%, rgba(187,0,51,0) 27%),
    radial-gradient(circle closest-side at 40% 43%, rgb(255, 211, 223) 26%, rgba(187,0,51,0) 27%),
    radial-gradient(circle closest-side at 40% 22%, rgb(248, 195, 206) 45%, rgba(221,51,85,0) 46%),
    radial-gradient(circle closest-side at 60% 22%, rgb(248, 195, 206) 45%, rgba(221,51,85,0) 46%),
    radial-gradient(circle closest-side at 50% 35%, rgb(248, 195, 206) 30%, rgba(221,51,85,0) 31%),

    radial-gradient(circle closest-side at 60% 43%, rgb(255, 211, 223) 26%, rgba(187,0,51,0) 27%) 50px 50px,
    radial-gradient(circle closest-side at 40% 43%, rgb(255, 211, 223) 26%, rgba(187,0,51,0) 27%) 50px 50px,
    radial-gradient(circle closest-side at 40% 22%, rgb(248, 195, 206) 45%, rgba(221,51,85,0) 46%) 50px 50px,
    radial-gradient(circle closest-side at 60% 22%, rgb(248, 195, 206) 45%, rgba(221,51,85,0) 46%) 50px 50px,
    radial-gradient(circle closest-side at 50% 35%, rgb(248, 195, 206) 30%, rgba(221,51,85,0) 31%) 50px 50px;
  background-color:rgb(255, 211, 223);
  background-size:100px 100px;
}

.card--valentines {
  background: #333;
  color: #444;
  font-family: 'Pacifico', cursive;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.card-title {
  font-size: 2.5rem;
  margin-bottom: 5rem;
}

.card-title--pulsing {
  color: #ccc;
}

.heart {
  background-color: #444;
  height: 10rem;
  width: 10rem;
  margin: 0 10px;
  top: 0;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.heart:before,
.heart:after {
  content: "";
  background-color: #444;
  border-radius: 50%;
  position: absolute;
  height: 10rem;
  width: 10rem;
}

.heart:before {
  top: -5rem;
  left: 0;
}

.heart:after {
  top: 0;
  left: 5rem;
}

.heart--pulsing {
  background-color: red;
  -webkit-animation: pulse 1s ease infinite;
          animation: pulse 1s ease infinite;
  -webkit-filter: drop-shadow(-0.3rem 0.3rem 0.2rem rgba(0, 0, 0, 0.2));
          filter: drop-shadow(-0.3rem 0.3rem 0.2rem rgba(0, 0, 0, 0.2));
}

.heart--pulsing:before,
.heart--pulsing:after {
  background-color: red;
}

.heart__text {
  position: absolute;
  width: 15rem;
  top: -5%;
  left: -10%;
  z-index: 1;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  color: white;
  font-size: 1.5rem;
  text-shadow: rgba(0, 0, 0, 0.5) 0.1rem 0.1rem 5px;
  -webkit-animation: swell 1s ease-in infinite;
          animation: swell 1s ease-in infinite;
}

.resistor-hole {
  margin-top: 4rem;
  position: relative;
  border: 0.2rem dashed #ccc;
  width: 70px;
  height: 30px;
}

.resistor-hole__label {
  font-family: 'Courier New', Courier, monospace;
  font-weight: bold;
  margin: 0;
  padding: 0 0.5rem;
  position: absolute;
  background-color: #333;
  color: #ccc;
  top: -0.5rem;
}

.resistor-hole__pad {
  position: absolute;
  border-radius: 50%;
  background-color: black;
  border: solid 3px #ccc;
  width: 0.4rem;
  height: 0.4rem;
}

.resistor-hole__clipper {
  margin-left: 10px;
  position: relative;
  width: 50px;
  height: 30px;
  overflow-x: hidden;
}

.resistor {
  position: absolute;
  width: 100px;
  z-index: 5;
  -webkit-filter: drop-shadow(-0.3rem 0.3rem 0.2rem rgba(0, 0, 0, 0.2));
          filter: drop-shadow(-0.3rem 0.3rem 0.2rem rgba(0, 0, 0, 0.2));
}

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1) rotate(-45deg);
            transform: scale(1) rotate(-45deg);
    -webkit-filter: drop-shadow(-0.3rem 0.3rem 0.2rem rgba(0, 0, 0, 0.2));
            filter: drop-shadow(-0.3rem 0.3rem 0.2rem rgba(0, 0, 0, 0.2));
  }
  25% {
    -webkit-transform: scale(1.05) rotate(-43deg);
            transform: scale(1.05) rotate(-43deg);
    -webkit-filter: drop-shadow(-0.4rem 0.4rem 0.3rem rgba(0, 0, 0, 0.2));
            filter: drop-shadow(-0.4rem 0.4rem 0.3rem rgba(0, 0, 0, 0.2));
  }
  50% {
    -webkit-transform: scale(1) rotate(-45deg);
            transform: scale(1) rotate(-45deg);
    -webkit-filter: drop-shadow(-0.3rem 0.3rem 0.2rem rgba(0, 0, 0, 0.2));
            filter: drop-shadow(-0.3rem 0.3rem 0.2rem rgba(0, 0, 0, 0.2));
  }
}

@keyframes pulse {
  0% {
    -webkit-transform: scale(1) rotate(-45deg);
            transform: scale(1) rotate(-45deg);
    -webkit-filter: drop-shadow(-0.3rem 0.3rem 0.2rem rgba(0, 0, 0, 0.2));
            filter: drop-shadow(-0.3rem 0.3rem 0.2rem rgba(0, 0, 0, 0.2));
  }
  25% {
    -webkit-transform: scale(1.05) rotate(-43deg);
            transform: scale(1.05) rotate(-43deg);
    -webkit-filter: drop-shadow(-0.4rem 0.4rem 0.3rem rgba(0, 0, 0, 0.2));
            filter: drop-shadow(-0.4rem 0.4rem 0.3rem rgba(0, 0, 0, 0.2));
  }
  50% {
    -webkit-transform: scale(1) rotate(-45deg);
            transform: scale(1) rotate(-45deg);
    -webkit-filter: drop-shadow(-0.3rem 0.3rem 0.2rem rgba(0, 0, 0, 0.2));
            filter: drop-shadow(-0.3rem 0.3rem 0.2rem rgba(0, 0, 0, 0.2));
  }
}

@-webkit-keyframes swell {
  0% { -webkit-transform: scale(1) rotate(45deg); transform: scale(1) rotate(45deg); }
  25% { -webkit-transform: scale(1.05) rotate(45deg); transform: scale(1.05) rotate(45deg); }
  55% { -webkit-transform: scale(1) rotate(45deg); transform: scale(1) rotate(45deg); }
}

@keyframes swell {
  0% { -webkit-transform: scale(1) rotate(45deg); transform: scale(1) rotate(45deg); }
  25% { -webkit-transform: scale(1.05) rotate(45deg); transform: scale(1.05) rotate(45deg); }
  55% { -webkit-transform: scale(1) rotate(45deg); transform: scale(1) rotate(45deg); }
}
.loading-text {
    font-family: 'Luckiest Guy';
    position: absolute;
}

#phaser-target {
    box-shadow: -10px 10px 20px 5px rgba(0, 0, 0, 0.4), 0px 0px 10px 1px rgba(0, 0, 0, 0.4);
    height: 600px;
}

.app__dino {
    background: url(/static/media/pattern.28bfddac.png), lightgreen;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

.nav-link {
    color: lightslategray;
    font-family: 'Pacifico';
    margin-right: 20px;
    -webkit-align-self: flex-end;
            align-self: flex-end;
}
